import './App.css';
import React, {Component} from 'react';
import Select from './components/'

export default class App extends Component { 
  constructor(props){
    super(props); 
    this.state = {
      value1: '',
      inputError: '',
      value2: ''
  };
    this.numbersToState = this.numbersToState.bind(this);
    this.typeOfOperation = this.typeOfOperation.bind(this);
  }

  typeOfOperation(event) {
    if (this.validate()) {
      let systemOfNum = document.querySelector('.select1').value,
      a = this.state.value1,
      b = document.querySelector('.select2').value;
      if ( systemOfNum == 10) { 
        this.fromTenToAny(a,b)
      } else 
      if ( b == 10 ) { 
        this.fromAnyToTen(a,systemOfNum);
      } else { 
        this.fromTenToAny(this.fromAnyToTen(a,systemOfNum), b);
      }
    }
  }

  fromAnyToTen(a,b){
    let finish=0;
    let uncrop = `${a}`.split(''), 
        beforePoint, afterPoint;
    if(uncrop.includes('.')) { 
      beforePoint = uncrop.slice(0,uncrop.indexOf('.')).reverse();
      afterPoint = uncrop.slice(uncrop.indexOf('.')+1);
    } else { 
      beforePoint = uncrop.reverse()
    }
    let sixteen = ['A', 'B', 'C', 'D', 'E', 'F'];
      beforePoint.forEach((item, i) => {
        sixteen.forEach((it, j) => { 
          if(it == item )  { 
            item = j+10;
          }
        })
        item *= b**i
        finish += item;
      })

    // let afterPoint = ((a%1).toFixed(5) + '').split('').slice(2);
    if(afterPoint !== undefined) { 
      afterPoint.forEach((item, i) => { 
        sixteen.forEach((it, j) => {  
          if(it == item) { 
            item = j+10;
          }
        })
        item *= b**(-(i+1));
        finish +=item;
     })
    }
    this.setState({value2: finish});
    return finish;
  }

  fromTenToAny(a,b) { 
    let arr = [],
    result, 
    sixteen = ['A', 'B', 'C', 'D', 'E', 'F'],
       num = (a%1);
      if (a>9 && a<16 & b>10) { 
       return sixteen.forEach((item, i) => (a == i+10 ?  this.setState({value2: item}) : ''));
      }
     while( num>0 && arr.length < 10) { 
      let wholePart = Math.trunc(num*b),
      sixteen = ['A', 'B', 'C', 'D', 'E', 'F'];
        if ( (wholePart) > 9 && (wholePart) <  16) { 
          sixteen.forEach((item, i) => ( wholePart == i+10 ? arr.push(item) : ''));
        } else { 
          arr.push(wholePart);
        } 
        num = (num*b)%1;
      }
      result = a%1 == 0 ? `${firstPart(a,b)}` : `${firstPart(a,b)}.${arr.join('')}`;
      this.setState({value2: result });
      // console.log(this.state.value2)

    function firstPart(a,b) { 
      let arr1 = [];
      while ( a!== 0) { 
      let num = Math.trunc(a)%b,
        sixteen = ['A', 'B', 'C', 'D', 'E', 'F'];
        if ( num > 9 && num <  16) { 
          sixteen.forEach((item, i) => ( num == i+10 ? arr1.push(item) : ''));
        } else { 
          arr1.push(num);
        } 
        a = Math.trunc(Math.trunc(a)/b);
      }
      return arr1.reverse().join('');
    }
  }

  numbersToState(event){
      this.setState({value1: event.target.value});
      
  }

  validate = () =>  { 
    let value1 = this.state.value1,
        select1 = document.querySelector('.select1').value,
        select2 = document.querySelector('.select2').value;
    if (!/^[0-9A-Fa-f.]+$/.test(value1)) { 
      this.setState({inputError : 'Please enter correct number'});
      return false;
    } else if(select1 == select2) {
      this.setState({inputError: 'Choose different system of caunting please!'});
      return false;
    } else { 
      this.setState({inputError: ''});
      return true;
    }
    
  }

  render() {
    const {value1}= this.state;
    return (
      <div className="App">
        <section className="content">
          <Select  selectClass='select1' label='Выберите систему счисления в которой записано число:'/>
          <label for="input1">Введите число</label>
          <div style={{color:'red'}}>{this.state.inputError}</div>
          <input className="input1" value={value1} onChange={this.numbersToState} ></input>
          <Select  selectClass='select2' label='Выберите в какую систему счисления хотите перевести число:'/> 
          <div className="bottomBlock">
            <span className="result" >{this.state.value2} </span>
            <button className="but" onClick={this.typeOfOperation}>Перевести</button>
          </div>
        </section>
      </div>
    );
  }
}


